html, body {
  background-color: #084261;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 24px;
  -webkit-font-smoothing: antialiased;
  color: rgba(255,255,255,0.8);
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

strong {
  font-weight: 500;
  color: rgba(255,255,255,1);
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  color: rgba(255,255,255,1);
  font-weight: 500;
}

h1 {
  font-size: 32px;
  line-height: 40px;
  margin-bottom: 8px;
}

h3 {
  font-size: 18px;
  line-height: 24px;
  margin-bottom: 4px;
}

p {
  margin: 0;
}


